import { createReducer } from '@reduxjs/toolkit'
import { DayStatistics, fetchDayStatistics, fetchKList, fetchRecentTransactions } from './actions'

export interface KListsState {
  readonly current?: [] | null
  readonly pendingUpdate?: [] | null
  readonly loadingRequestId: string | null
  readonly error: string | null
}
export interface DayStatisticsState {
  readonly current: DayStatistics
  readonly pendingUpdate?: DayStatistics | null
  readonly loadingRequestId: string | null
  readonly error: string | null
}

const initialDayStatistics = {
  action1_token1: 0,
  action2_token1: 0,
  action3_token1: 0,
  action4_token1: 0,

  action1_token2: 0,
  action2_token2: 0,
  action3_token2: 0,
  action4_token2: 0,

  token1_fee: 0,
  token2_fee: 0,
}
export interface PoolAnalysisState {
  kline: KListsState
  recent_transactions: KListsState
  day_statistics: DayStatisticsState
}

export const initialState: PoolAnalysisState = {
  kline: {
    error: null,
    current: null,
    loadingRequestId: null,
    pendingUpdate: null,
  },
  recent_transactions: {
    error: null,
    current: null,
    loadingRequestId: null,
    pendingUpdate: null,
  },
  day_statistics: {
    error: null,
    current: initialDayStatistics,
    loadingRequestId: null,
    pendingUpdate: null,
  },
}

export default createReducer(initialState, (builder) =>
  builder
    .addCase(fetchKList.pending, (state, { payload: { requestId } }) => {
      state.kline = {
        current: null,
        pendingUpdate: null,
        ...state,
        loadingRequestId: requestId,
        error: null,
      }
    })
    .addCase(fetchKList.fulfilled, (state, { payload: { requestId, kList } }) => {
      const current = state?.kline?.current
      const loadingRequestId = state?.kline?.loadingRequestId

      // no-op if update does nothing
      if (current) {
        if (loadingRequestId === null || loadingRequestId === requestId) {
          state.kline = {
            loadingRequestId: null,
            error: null,
            current: current,
            pendingUpdate: kList,
          }
        }
      } else {
        state.kline = {
          loadingRequestId: null,
          error: null,
          current: kList,
          pendingUpdate: null,
        }
      }

      return state
    })
    .addCase(fetchKList.rejected, (state, { payload: { url, requestId, errorMessage } }) => {
      if (state?.kline?.loadingRequestId !== requestId) {
        // no-op since it's not the latest request
        return
      }

      state.kline = {
        loadingRequestId: null,
        error: errorMessage,
        current: null,
        pendingUpdate: null,
      }
      return state
    })
    .addCase(fetchRecentTransactions.pending, (state, { payload: { requestId } }) => {
      state.recent_transactions = {
        current: null,
        pendingUpdate: null,
        ...state,
        loadingRequestId: requestId,
        error: null,
      }
    })
    .addCase(fetchRecentTransactions.fulfilled, (state, { payload: { requestId, data } }) => {
      const current = state?.recent_transactions?.current
      const loadingRequestId = state?.recent_transactions?.loadingRequestId

      // no-op if update does nothing
      if (current) {
        if (loadingRequestId === null || loadingRequestId === requestId) {
          state.recent_transactions = {
            loadingRequestId: null,
            error: null,
            current: current,
            pendingUpdate: data,
          }
        }
      } else {
        state.recent_transactions = {
          loadingRequestId: null,
          error: null,
          current: data,
          pendingUpdate: null,
        }
      }

      return state
    })
    .addCase(fetchRecentTransactions.rejected, (state, { payload: { url, requestId, errorMessage } }) => {
      if (state?.recent_transactions?.loadingRequestId !== requestId) {
        // no-op since it's not the latest request
        return
      }

      state.recent_transactions = {
        loadingRequestId: null,
        error: errorMessage,
        current: null,
        pendingUpdate: null,
      }
      return state
    })
    .addCase(fetchDayStatistics.pending, (state, { payload: { requestId } }) => {
      state.day_statistics = {
        current: initialDayStatistics,
        pendingUpdate: null,
        ...state,
        loadingRequestId: requestId,
        error: null,
      }
    })
    .addCase(fetchDayStatistics.fulfilled, (state, { payload: { requestId, data } }) => {
      const current = state?.day_statistics?.current
      const loadingRequestId = state?.day_statistics?.loadingRequestId
      // no-op if update does nothing
      if (loadingRequestId === null || loadingRequestId === requestId) {
        state.day_statistics = {
          loadingRequestId: null,
          error: null,
          current: data,
          pendingUpdate: data,
        }
      }
      return state
    })
    .addCase(fetchDayStatistics.rejected, (state, { payload: { url, requestId, errorMessage } }) => {
      if (state?.day_statistics?.loadingRequestId !== requestId) {
        // no-op since it's not the latest request
        return
      }

      state.day_statistics = {
        loadingRequestId: null,
        error: errorMessage,
        current: initialDayStatistics,
        pendingUpdate: null,
      }
      return state
    })
)
