import { Trans } from '@lingui/macro';
import DoubleCurrencyLogo from 'components/DoubleLogo';
import { NetworkAlert } from 'components/NetworkAlert/NetworkAlert';
import { V2_FACTORY_ADDRESSES } from 'constants/addresses';
import { useCurrency } from 'hooks/Tokens';
import useToggledVersion from 'hooks/useToggledVersion';
import { useEffect, useMemo, useState } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { WrappedTokenInfo } from 'state/lists/wrappedTokenInfo';
import { useFetchDayStatisticsCallback } from 'state/poolAnalysis/useFetchCallback';
import { useDerivedSwapInfo } from 'state/swap/hooks';
import { computePairAddress } from 'v2-sdk/entities/pair';
import { useDerivedMintInfo } from '../../state/mint/hooks';
import AppBody from '../AppBody';
import { PoolData, RecentTransactionsDiv, SubTitle, Title } from './Components';
import KChart from './KChart';
import RecentTransactions from './RecentTransactions';



export default function Home({
  match: {
    params: { currencyIdA, currencyIdB },
  } }: RouteComponentProps<{ currencyIdA?: string; currencyIdB?: string }>) {

  const toggledVersion = useToggledVersion()

  const { currencies } = useDerivedSwapInfo(toggledVersion);


  const [currencyTab, setCurrencyTab] = useState('history');
  const cA = useCurrency(currencyIdA) as WrappedTokenInfo;
  const cB = useCurrency(currencyIdB) as WrappedTokenInfo;
  const currencyA = currencyIdA ? cA : (currencies['INPUT']) as WrappedTokenInfo
  const currencyB = currencyIdB ? cB : (currencies['OUTPUT']) as WrappedTokenInfo




  const { pair } = useDerivedMintInfo(currencyA ?? undefined, currencyB ?? undefined)
  const tabs = useMemo(() => {
    const k = <span onClick={() => setCurrencyTab('kline')}><Trans>K line</Trans></span>;
    const history = <span onClick={() => setCurrencyTab('history')}><Trans>Recent Transactions</Trans></span>;
    const tabs = currencyTab == 'kline' ? <>{k}{history}</> : <>{history}{k}</>;
    return tabs;
  }, [currencyTab]);


  const pairAddresses = useMemo(
    () => {
      const tokenA = currencyA?.wrapped;
      const tokenB = currencyB?.wrapped;
      if (currencyA && currencyB) {
        const fa = currencyA.chainId === currencyB.chainId &&
          !currencyA.equals(currencyB) &&
          V2_FACTORY_ADDRESSES[currencyA.chainId];
        if (fa) {
          const rst = computePairAddress({ factoryAddress: fa, tokenA, tokenB });
          // console.log(`fa:${fa},rst:${rst},tokenA=${currencyA.address},tokenB=${currencyB.address}`);
          return rst;
        }
      }
      return undefined;
    },
    [currencyA, currencyB]
  )
  const getDayStatistics = useFetchDayStatisticsCallback();
  useEffect(() => {
    getDayStatistics(`/api/Fst/get_day_statistics?pair_address=${pairAddresses}`);
  }, [getDayStatistics, pairAddresses]);

  console.log('pairAddresses', pairAddresses)
  return (<>
    <NetworkAlert />
    <AppBody>
      <Title>
        <div>
          {pair && <DoubleCurrencyLogo currency0={currencyA} currency1={currencyB} size={24} margin={true} />}
          {/* <img src={currencyA?.logoURI} style={{ width: '25px' }}></img>
          <img src={currencyB?.logoURI} style={{ width: '25px', margin: '0 10px 0 -10px' }}></img> */}
          <span style={{ marginLeft: '10px' }}>{currencyA?.symbol}-{currencyB?.symbol}</span>
        </div>
        {/* <span className="right"></span> */}
      </Title>
      {pair ? <PoolData pair={pair} /> : <div style={{ padding: '2rem 1rem' }}><Trans>The pair was not found in the pool...</Trans></div>}
      {/* <RowComp label="" valueProps={{ num: 0 }} />
      <RowComp label="" valueProps={{ num: 0 }} /> */}
      <SubTitle style={{ padding: '1.8rem 1rem 1rem 0rem' }}>
        <span className={currencyTab == 'history' ? 'active' : 'normal'} onClick={() => setCurrencyTab('history')}><Trans>Recent Transactions</Trans></span>
        {/* <span className={currencyTab == 'kline' ? 'active' : 'normal'} onClick={() => setCurrencyTab('kline')}><Trans>K line</Trans></span> */}
      </SubTitle>
      <div style={{ display: currencyTab != 'kline' ? 'none' : 'block' }}>
        <KChart pairAddresses={pairAddresses} name={`${currencyA?.symbol}-${currencyB?.symbol}`} /></div>
      <RecentTransactionsDiv style={{ display: currencyTab == 'kline' ? 'none' : 'block' }}>
        <RecentTransactions pairAddresses={pairAddresses} name={`${currencyA?.symbol}-${currencyB?.symbol}`} />
      </RecentTransactionsDiv>
    </AppBody>
  </>
  )
}