type props = {
  label: string | any,
  valueProps?: NumberTextProps
}
export const RowComp = (props: props) => {
  const { label, valueProps } = props;
  return <Row>
    <Label>{label}</Label>
    <Text>
      <NumberText {...valueProps} />
    </Text>
  </Row>
}
type cardProps = {
  left: props,
  right: props
}
export const RowCard = (props: cardProps) => {
  const { left, right } = props;
  return <div style={{ display: 'flex', justifyContent: 'space-around', flexDirection: 'row', padding: '0' }}>
    <CardNumber rigthBorder style={{ padding: '0.6rem 0' }}>
      <Label><Trans>{left.label}</Trans></Label>
      <Text>
        <NumberText {...left.valueProps} />
      </Text>
    </CardNumber>
    <CardNumber style={{ padding: '0.6rem 0' }}>
      <Label><Trans>{right.label}</Trans></Label>
      <Text>
        <NumberText {...right.valueProps} />
      </Text>
    </CardNumber>
  </div>
}

interface PositionCardProps {
  pair: Pair
}

export const PoolData = ({ pair }: PositionCardProps) => {
  const { account } = useActiveWeb3React()
  const dayStatistics = useDayStatistics();
  const currency0 = unwrappedToken(pair.token0)
  const currency1 = unwrappedToken(pair.token1)

  const currency0Balance = useCurrencyBalance(account ?? undefined, currency0 ?? undefined);
  const currency1Balance = useCurrencyBalance(account ?? undefined, currency1 ?? undefined);

  // const userDefaultPoolBalance = useTokenBalance(account ?? undefined, pair.liquidityToken)
  const totalPoolTokens = useTotalSupply(pair.liquidityToken)

  const userPoolBalance = useTokenBalance(account ?? undefined, pair.liquidityToken)
  const info = useV2Pair(currency0, currency1);
  // if staked balance balance provided, add to standard liquidity amount
  // const userPoolBalance = stakedBalance ? userDefaultPoolBalance?.add(stakedBalance) : userDefaultPoolBalance
  // debugger
  const poolTokenPercentage =
    !!userPoolBalance &&
      !!totalPoolTokens &&
      JSBI.greaterThanOrEqual(totalPoolTokens.quotient, userPoolBalance.quotient)
      ? new Percent(userPoolBalance.quotient, totalPoolTokens.quotient)
      : undefined

  const [token0Deposited, token1Deposited] =
    !!pair &&
      !!totalPoolTokens &&
      !!userPoolBalance &&
      // this condition is a short-circuit in the case where useTokenBalance updates sooner than useTotalSupply
      JSBI.greaterThanOrEqual(totalPoolTokens.quotient, userPoolBalance.quotient)
      ? [
        pair.getLiquidityValue(pair.token0, totalPoolTokens, userPoolBalance, false),
        pair.getLiquidityValue(pair.token1, totalPoolTokens, userPoolBalance, false),
      ]
      : [undefined, undefined]
  console.log('dayStatistics', dayStatistics)
  const token1Vol = useMemo(() => (dayStatistics.action3_token1 + dayStatistics.action4_token1), [dayStatistics.action3_token1, dayStatistics.action4_token1])
  const token2Vol = useMemo(() => (dayStatistics.action3_token2 + dayStatistics.action4_token2), [dayStatistics.action3_token2, dayStatistics.action4_token2])

  const unitPrice = useMemo(() => {
    if (['USDT', "BNB"].includes(currency0.symbol || '')) {
      return {
        coin: currency1.symbol,
        money: currency0.symbol,
        num: parseFloat(info?.[1]?.reserve0?.toSignificant(10) || '0') / parseFloat(info?.[1]?.reserve1?.toSignificant(10) || '0')
      }
    } else if (['USDT', "BNB"].includes(currency1.symbol || '')) {
      return {
        coin: currency0.symbol,
        money: currency1.symbol,
        num: parseFloat(info?.[1]?.reserve1?.toSignificant(10) || '0') / parseFloat(info?.[1]?.reserve0?.toSignificant(10) || '0')
      }
    }
    return {
      coin: '',
      money: '',
      num: 0
    };
  }, [currency0.symbol, currency1.symbol, info]);

  const total = useMemo(() => {
    debugger
    if (['USDT', "BNB"].includes(currency0.symbol || '')) {
      const u = parseFloat(currency0Balance?.toSignificant(10) || '0') + parseFloat(token0Deposited?.toSignificant(10) || '0');
      const c = (parseFloat(currency1Balance?.toSignificant(10) || '0') + parseFloat(token1Deposited?.toSignificant(10) || '0')) * unitPrice.num;
      return (u || 0) + (c || 0);
    } else if (['USDT', "BNB"].includes(currency1.symbol || '')) {
      const u = parseFloat(currency1Balance?.toSignificant(10) || '0') + parseFloat(token1Deposited?.toSignificant(10) || '0');
      const c = (parseFloat(currency0Balance?.toSignificant(10) || '0') + parseFloat(token0Deposited?.toSignificant(10) || '0')) * unitPrice.num;
      return (u || 0) + (c || 0);
    }
    return 0;
  }, [currency0.symbol, currency0Balance, currency1.symbol, currency1Balance, token0Deposited, token1Deposited, unitPrice]);

  return <>
    <SubTitle><Trans>Your account balance</Trans></SubTitle>
    <RowCard
      left={{ label: currency0.symbol, valueProps: { num: currency0Balance?.toSignificant(10), format: '0,0.000000' } }}
      right={{ label: currency1.symbol, valueProps: { num: currency1Balance?.toSignificant(10), format: '0,0.000000' } }}
    />
    {unitPrice.num > 0 && <RowComp label={<Trans>Your total assets:</Trans>} valueProps={{ num: total, format: '0,0.000000', b: '', a: unitPrice.money }} />}
    {unitPrice.num > 0 && <RowComp label={<Trans>Currency price:</Trans>} valueProps={{ num: unitPrice.num, format: '0,0.000000', b: `1 ${unitPrice.coin}  `, a: unitPrice.money }} />}
    <SubTitle><Trans>Your total pool</Trans></SubTitle>

    <RowCard
      left={{ label: currency0.symbol, valueProps: { num: token0Deposited?.toSignificant(10), format: '0,0.000000' } }}
      right={{ label: currency1.symbol, valueProps: { num: token1Deposited?.toSignificant(10), format: '0,0.000000' } }}
    />
    <RowCard
      left={{
        label: <Trans>Estimate Fee(24hr)</Trans>,
        valueProps: {
          num: (dayStatistics?.token1_fee || 0) * parseFloat(poolTokenPercentage?.toFixed(10) || '0') / 100,
          format: '0,0.000000'
        }
      }}
      right={{
        label: <Trans>Estimate Fee(24hr)</Trans>,
        valueProps: {
          num: (dayStatistics?.token2_fee || 0) * parseFloat(poolTokenPercentage?.toFixed(10) || '0') / 100,
          format: '0,0.000000'
        }
      }}
    />
    <RowComp label={<Trans>Your total pool tokens:</Trans>} valueProps={{ num: userPoolBalance?.toFixed(4), format: '0,0.000000' }} />
    <RowComp label={<Trans>Your pool share:</Trans>} valueProps={{ a: '%', num: poolTokenPercentage?.toFixed(6), format: '0,0.000000' }} />

    <SubTitle><Trans>Total pool</Trans></SubTitle>
    <RowCard
      left={{ label: currency0.symbol, valueProps: { num: info?.[1]?.reserve0?.toSignificant(10), format: '0,0.000000' } }}
      right={{ label: currency1.symbol, valueProps: { num: info?.[1]?.reserve1?.toSignificant(10), format: '0,0.000000' } }}
    />
    <RowCard
      left={{ label: <Trans>Volume(24hr)</Trans>, valueProps: { num: token1Vol, format: '0,0.000000' } }}
      right={{ label: <Trans>Volume(24hr)</Trans>, valueProps: { num: token2Vol, format: '0,0.000000' } }}
    />
    <RowCard
      left={{ label: <Trans>LP Fee(24hr)</Trans>, valueProps: { num: dayStatistics.token1_fee, format: '0,0.000000' } }}
      right={{ label: <Trans>LP Fee(24hr)</Trans>, valueProps: { num: dayStatistics.token2_fee, format: '0,0.000000' } }}
    />
    <RowComp label={<Trans>Total pool tokens: </Trans>} valueProps={{ num: totalPoolTokens?.toSignificant(10), format: '0,0.000000' }} />
  </>
}


