import { useAppSelector } from 'state/hooks'
import { DayStatistics } from './actions'

export function useKLists(): [] {
  return useAppSelector((state) => state.poolAnalysis?.kline?.current || [])
}
export function useRecentTransactions(): [] {
  return useAppSelector((state) => state.poolAnalysis?.recent_transactions?.current || [])
}

export function useDayStatistics(): DayStatistics {
  return useAppSelector((state) => state.poolAnalysis?.day_statistics?.current)
}
